<template>
  <div class="col-6">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">相似案件</div>
      </div>
      <div class="panel-body">
        <div class="similar">
          <dl>
            <dd :class="{'active':showPage==1}" @click="showPage=1,init()">已结案</dd>
            <dd :class="{'active':showPage==2}" @click="showPage=2,init()">未结案</dd>
          </dl>
        </div>
        <div class="tablescoll tablescollstyle">
          <el-row>
            <el-table :data='item' stripe height="210" ref='table' style='width: 100%' class="table table-striped"
              v-loading="loading">
              <el-table-column prop='caseFacts' min-width="200" show-overflow-tooltip label='案件名称' fixed>
              </el-table-column>
              <el-table-column prop='illegalBehvr' min-width="100" show-overflow-tooltip label='主要违法行为'>
              </el-table-column>
              <el-table-column prop='illegalTypes' min-width="100" show-overflow-tooltip label='违法类型'>
              </el-table-column>
              <el-table-column prop='punishLink' min-width="100" show-overflow-tooltip label='查处环节'>
              </el-table-column>
              <el-table-column width="80" show-overflow-tooltip label='相似度' fixed="right">
                <template slot-scope="scope">
                  {{scope.row.siminal}}%
                </template>
              </el-table-column>
            </el-table>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      showPage: 1,
      item: []
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      let caseStatus = '已结案'
      if (this.showPage === 2) {
        caseStatus = '办理中'
      }
      this.item = []
      this.$http.get({
        api: 'A_S_CASE_SIMILAR',
        params: {
          caseId: this.$route.params.id,
          caseStatus: caseStatus
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.item = res
        }
      })
    }
  }
}

</script>

<style scoped>
  .el-table--fit {
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
  }

</style>



