<template>
  <div class="col-3">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">案件画像</div>
      </div>
      <div class="panel-body" v-loading="loading" style="overflow: auto;">
        <div class="chart" id="info_chart2"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_CASE_WORLD',
        params: {
          caseId: this.$route.params.id
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.draw(res)
        }
      })
    },
    draw (a) {
      let option = {
        tooltip: {
          show: true,
          formatter: '{b0}'
        },
        series: [{
          name: '事件画像',
          type: 'wordCloud',
          size: ['80%', '80%'],
          textRotation: [0, 45, 90, -45],
          textPadding: 0,
          autoSize: {
            enable: true,
            minSize: 14
          },
          textStyle: this.createRandomItemStyle(),
          data: a
        }]
      }
      // 为echarts对象加载数据
      let myChart = this.$echarts.init(document.getElementById('info_chart2'))
      myChart.setOption(option)
    },
    createRandomItemStyle () {
      return {
        normal: {
          color: function color () {
            return (
              'rgb(' + [
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160)
              ].join(',') +
                ')'
            )
          }
        }
      }
    }
  }
}

</script>



