<template>
  <div class="col-6">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">案件关联分析</div>
      </div>
      <div class="panel-body">
        <div class="choseinput">
          <dl>
            <dd>
              <el-checkbox v-model="period" @change="redraw">同时期</el-checkbox>
            </dd>
            <dd>
              <el-checkbox v-model="area" @change="redraw">同区域</el-checkbox>
            </dd>
            <dd>
              <el-checkbox v-model="sameType" @change="redraw">同类型</el-checkbox>
            </dd>
          </dl>
        </div>
        <div class="chart" id="info_chart5" style=" height:260px;"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      period: false,
      area: false,
      sameType: false,
      relationData: {}
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_CASE_RELATION',
        params: {
          caseId: this.$route.params.id
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.relationData = res
          self.redraw()
        }
      })
    },
    draw (a) {
      let self = this
      a = this.formatLink(a)
      var width = 800
      var height = 260
      var svg = window.d3.select('#info_chart5')
        .append('svg')
        .attr('width', width)
        .attr('height', height)
      var force = window.d3.layout.force().nodes(a.node)
        .links(a.link)
        .size([width, height])
        .linkDistance(150)
        .charge([-1000])

      force.start()

      var svgLink = svg.selectAll('line')
        .data(a.link)
        .enter()
        .append('line')
        .style('stroke', '#999')
        .style('stroke-width', 2)

        // 添加company节点
      var companyNodes = svg.selectAll('.companyCircle')
        .data(a.companyNode)
        .enter()
        .append('circle')
        .attr('class', 'companyCircle')
        .attr('r', 40)
        .style('fill', '#659AE6')
        .call(force.drag)
        .on('mouseover', function (d) {
          let event = window.d3.event
          self.$lib.tip(event.clientX, event.clientY, d.name)
        })
        .on('mouseout', function (d) {
          self.$lib.hideTip()
        })
        // 添加company文字
      var companyTexts = svg.selectAll('.companyText')
        .data(a.companyNode)
        .enter()
        .append('text')
        .attr('class', 'companyText')
        .style('fill', '#fff')
        .attr('dx', -12)
        .attr('dy', 5)
        .text(function (d) {
          if (d.name.length > 2) return d.name.substring(0, 2) + '...'
          return d.name
        })
        .on('mouseover', function (d) {
          let event = window.d3.event
          self.$lib.tip(event.clientX, event.clientY, d.name)
        })
        .on('mouseout', function (d) {
          self.$lib.hideTip()
        })
        // 添加self节点
      var selfNodes = svg.selectAll('.selfCircle')
        .data(a.selfNode)
        .enter()
        .append('circle')
        .attr('class', 'selfCircle')
        .attr('r', 30)
        .style('fill', '#FF8A32')
        .call(force.drag)
        .on('mouseover', function (d) {
          let event = window.d3.event
          self.$lib.tip(event.clientX, event.clientY, d.name)
        })
        .on('mouseout', function (d) {
          self.$lib.hideTip()
        })
        // 添加self文字
      var selfTexts = svg.selectAll('.selfText')
        .data(a.selfNode)
        .enter()
        .append('text')
        .attr('class', 'selfText')
        .style('fill', '#fff')
        .attr('dx', -12)
        .attr('dy', 5)
        .text(function (d) {
          if (d.name.length > 2) return d.name.substring(0, 2) + '...'
          return d.name
        })
        .on('mouseover', function (d) {
          let event = window.d3.event
          self.$lib.tip(event.clientX, event.clientY, d.name)
        })
        .on('mouseout', function (d) {
          self.$lib.hideTip()
        })
        // 添加other节点
      var otherNodes = svg.selectAll('.otherCircle')
        .data(a.otherNode)
        .enter()
        .append('circle')
        .attr('class', 'otherCircle')
        .attr('r', 30)
        .style('fill', '#1EC6C9')
        .call(force.drag)
        .on('mouseover', function (d) {
          self.$lib.tip(event.clientX, event.clientY, d.name)
        })
        .on('mouseout', function (d) {
          self.$lib.hideTip()
        })
        // 添加other文字
      var otherTexts = svg.selectAll('.otherText')
        .data(a.otherNode)
        .enter()
        .append('text')
        .attr('class', 'otherText')
        .style('fill', '#fff')
        .attr('dx', -12)
        .attr('dy', 5)
        .text(function (d) {
          if (d.name.length > 2) return d.name.substring(0, 2) + '...'
          return d.name
        })
        .on('mouseover', function (d) {
          let event = window.d3.event
          self.$lib.tip(event.clientX, event.clientY, d.name)
        })
        .on('mouseout', function (d) {
          self.$lib.hideTip()
        })

      force.on('tick', function () { // 对于每一个时间间隔
        // 更新连线坐标
        svgLink.attr('x1', function (d) {
          return d.source.x
        })
          .attr('y1', function (d) {
            return d.source.y
          })
          .attr('x2', function (d) {
            return d.target.x
          })
          .attr('y2', function (d) {
            return d.target.y
          })
          // 更新节点坐标
        companyNodes.attr('cx', function (d) {
          return d.x
        })
          .attr('cy', function (d) {
            return d.y
          })
          // 更新文字坐标
        companyTexts.attr('x', function (d) {
          return d.x
        })
          .attr('y', function (d) {
            return d.y
          })
          // 更新节点坐标
        selfNodes.attr('cx', function (d) {
          return d.x
        })
          .attr('cy', function (d) {
            return d.y
          })
          // 更新文字坐标
        selfTexts.attr('x', function (d) {
          return d.x
        })
          .attr('y', function (d) {
            return d.y
          })
          // 更新节点坐标
        otherNodes.attr('cx', function (d) {
          return d.x
        })
          .attr('cy', function (d) {
            return d.y
          })
          // 更新文字坐标
        otherTexts.attr('x', function (d) {
          return d.x
        })
          .attr('y', function (d) {
            return d.y
          })
      })
    },
    formatLink (a) {
      var arr = []
      a.otherNode = []
      a.node.map(o => {
        arr.push(o.value)
        if (o.isMain === 2) a.otherNode.push(o)
      })
      arr.map((o, i) => {
        a.link.map(o2 => {
          if (o === o2.source) o2.source = i
          if (o === o2.target) o2.target = i
        })
      })
      return a
    },
    redraw () {
      document.querySelector('#info_chart5').innerHTML = ''
      let a = JSON.parse(JSON.stringify(this.relationData))
      let d = this.getData(a)
      this.draw(d)
    },
    getData (a) {
      let arr = {
        companyNode: [],
        selfNode: [],
        otherNode: [],
        node: [],
        link: []
      }
      let companyNodeValue = []
      let linkValue = []
      a.node.map(o => {
        if (o.isMain === 10 && this.period) {
          companyNodeValue.push(o.value)
          arr.companyNode.push(o)
        }
        if (o.isMain === 11 && this.area) {
          companyNodeValue.push(o.value)
          arr.companyNode.push(o)
        }
        if (o.isMain === 13 && this.sameType) {
          companyNodeValue.push(o.value)
          arr.companyNode.push(o)
        }
        if (o.isMain === 1) arr.selfNode = [o]
      })
      if (arr.companyNode.length === 0) {
        arr.node = arr.selfNode
        return arr
      }
      a.link.map(o => {
        if (companyNodeValue.indexOf(o.source) > -1 || companyNodeValue.indexOf(o.target) > -1) {
          arr.link.push(o)
          if (linkValue.indexOf(o.source) === -1) {
            linkValue.push(o.source)
          }
          if (linkValue.indexOf(o.target) === -1) {
            linkValue.push(o.target)
          }
        }
      })
      a.node.map(o => {
        if (linkValue.indexOf(o.value) > -1) {
          arr.node.push(o)
        }
      })
      return arr
    }
  }
}

</script>



