<template>
  <div class="col-6">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">案件基本信息</div>
      </div>
      <div class="panel-body" style="overflow: auto; height: 295px;">
        <table class="table table-striped">
          <tbody>
            <tr>
              <td>案件名称</td>
              <td>{{item.caseFacts}}</td>
              <td>案件类型</td>
              <td>{{item.illegalBehvr}}</td>
              <td>办理状态</td>
              <td><span class="badge badge-primary">{{item.caseStat}}</span></td>
            </tr>
            <tr>
              <td>办案机构</td>
              <td></td>
              <td>涉案企业</td>
              <td>{{item.entName}}</td>
              <td>立案时间</td>
              <td>{{item.regCaseTm}}</td>
            </tr>
            <tr>
              <td>查处环节</td>
              <td>{{item.punishLink}}</td>
              <td>涉案物品</td>
              <td></td>
              <td>涉案物品规格型号</td>
              <td></td>
            </tr>
            <tr>
              <td>涉案物品数量</td>
              <td></td>
              <td>涉案货值</td>
              <td>￥{{item.prodPrice}}</td>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td>主要违法行为</td>
              <td colspan="5">{{item.caseBehvr}}</td>
            </tr>
            <tr>
              <td>是否大案要案</td>
              <td colspan="5">{{item.punishLevel}}</td>
            </tr>
            <tr>
              <td>处罚结果</td>
              <td colspan="5"></td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      item: {}
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_CASE_INFO',
        params: {
          caseId: this.$route.params.id
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.item = res
          self.$bus.$emit('lawcaseinfoCompanyWorld', res)
        }
      })
    }
  }
}

</script>



