<template>
  <div class="col-6">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">决策分析</div>
      </div>
      <div class="panel-body">
        <div class="col-7">
          <div class="chart" id="info_chart7" style=" max-height:260px;"></div>
          <!-- <img src="/analysis/static/assets/images/test-e4.png" style=" height:260px;"> -->
        </div>
        <div class="col-5">
          <div class="tuijian">
            <h5>决策推荐</h5>
            <dl>
              <dd v-for="(i,index) in item.punishResult" :key="index">
                <p><em>{{i.name}}:</em><em>{{i.value}}</em></p>
              </dd>
            </dl>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      item: {}
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_CASE_DECISION_ITEM',
        params: {
          caseId: this.$route.params.id
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.item = res
          self.draw(res)
        }
      })
    },
    draw (a) {
      let d = this.dataFormat(a)
      let option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        series: d.series
      }
      let myChart = this.$echarts.init(document.getElementById('info_chart7'), 'macarons')
      myChart.setOption(option)
    },
    dataFormat (a) {
      let obj = {
        series: []
      }
      Array.prototype.slice.apply(a.resultAnalysisOuts).map((o, i) => {
        let center = ['16.6%', '25%']
        switch (i) {
          case 1:
            center = ['50%', '25%']
            break
          case 2:
            center = ['83.3%', '25%']
            break
          case 3:
            center = ['16.6%', '75%']
            break
          case 4:
            center = ['50%', '75%']
            break
          case 5:
            center = ['83.3%', '75%']
            break
        }
        obj.series.push({
          name: o.name,
          type: 'pie',
          radius: ['32%', '42%'],
          center: center,
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: true,
              position: 'center',
              formatter: function (params) {
                if (params.seriesName.length > 6) {
                  params.seriesName = params.seriesName.substring(0, 6) + '\n\r' + params.seriesName.substring(
                    6)
                }
                return params.seriesName
              }
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '12'
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: o.data
        })
      })
      return obj
    }
  }
}

</script>

<style>
  .tuijian dd {
    margin-bottom: 5px;
    padding: 6px;
  }

</style>



